body {
  background: #22c1c3;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fdbb2d, #22c1c3);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#fdbb2d), to(#22c1c3));
  background: -webkit-linear-gradient(left, #fdbb2d, #22c1c3);
  background: -o-linear-gradient(left, #fdbb2d, #22c1c3);
  background: linear-gradient(to right, #fdbb2d, #22c1c3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.App-header {
  background-color: #222;
  height: 80px;
  padding: 20px;
  color: white;
}

.button {
  font-size: 1.5rem;
  width: 250px;
  padding: 20px;
  background-color: transparent;
  border: solid white 2px;
  color: white;
  border-radius: 8px;
}

.title {
  color: white;
  font-size: 4rem;
  text-decoration: underline;
}

.spoiler-text {
  font-size: 3rem;
  color: #f5f5f5;
}

.App-body {
  text-align: center;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}




